























   
    import {
        mapState,
        mapMutations
    } from 'vuex'
	
    import mInput from '../../../components/m-input.vue'

    export default {
        components: {
            mInput
        },
        data() {
            return {
                providerList: [],
                hasProvider: false,
                account: '',
                password: '',
                positionTop: 0
            }
        },
        computed: {
			...mapState(['forcedLogin','token','username','data'])
			},
        methods: {
            ...mapMutations(['login','getuserinfo','getHeader','getNotice']),
            
            initPosition() {
                /**
                 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
                 * 反向使用 top 进行定位，可以避免此问题。
                 */
                this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
            },
            bindLogin() {
                /**
                 * 客户端对账号信息进行一些必要的校验。
                 * 实际开发中，根据业务需要进行处理，这里仅做示例。
                 */
                if (this.account.length != 13) {
                    uni.showToast({
                        icon: 'none',
                        title: '请输入正确的学号'
                    });
                    return;
                }
                
                
                /**
                 * 下面简单模拟下服务端的处理
                 * 检测用户账号密码是否在已注册的用户列表中
                 * 实际开发中，使用 uni.request 将账号信息发送至服务端，客户端在回调函数中获取结果信息。
                 */
               // const datas = {
               //     username1: this.account,
                  //  password1: this.password
               // };
				console.log('已输入'+this.account+this.password);
				uni.showLoading({
					title: '正在登录',
					mask: false
				});
				//与服务器进行验证
				uni.request({
					url: 'https://api.thinker.ink/v1/users/login/',
					method: 'POST',
					header:{
						'content-type':'application/json'
					},
					data: {
						username: this.account,
						password: this.password
					},
					success: res => {
						
						console.log('成功返回的信息-------------');
						console.log(res.data.data);
						this.login(res);
						uni.hideLoading();
						if(this.token) {
							
						    //getuserinfo();
							uni.showLoading({
								title: '正在获取信息',
								mask: false
							});
							console.log(this.token);
							this.getHeader();
							this.getuserinfo();
							this.getNotice();
							uni.hideLoading();
							uni.navigateBack();
						}else{
							console.log("login失败");
						}
                }})
				//this.login(data);
				//this.getuserinfo();
				
            }
			},                   
        onLoad() {
            this.initPosition();
           
        }
    }
